<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder="" v-model="search"> <el-button slot="append" icon="el-icon-search"></el-button> </el-input>
        </el-col>
        <el-col :span="2"><el-button type="success" @click="getHostList()"></el-button></el-col>
        <el-col :span="3"><el-button type="danger" @click="cycleRunFunc(0)"></el-button></el-col>
      </el-row>
      <el-table ref="multipleTable" :data="hostList" row-key="mac" border style="width: 100%">
        <el-table-column type="selection" :reserve-selection="true" width="45"> </el-table-column>
        <el-table-column type="index" label="ID"></el-table-column>
        <el-table-column prop="sn" label="SN" width="180"> </el-table-column>
        <el-table-column prop="vender" label=""> </el-table-column>
        <el-table-column prop="product" label=""> </el-table-column>
        <el-table-column prop="cpuinfo" label="CPU"> </el-table-column>
        <el-table-column prop="meminfo" label=""> </el-table-column>
        <el-table-column prop="clientip" label="Client IP"> </el-table-column>
        <el-table-column prop="ipmi" label="IPMI IP"> </el-table-column>
        <!-- <el-table-column prop="date" label=""> </el-table-column> -->
        <el-table-column prop="date" label="" width="180">
          <template #default="{ row }">
            <span> {{ row.date | dateFmt }}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="200px">
          <template slot-scope="scope">
            <el-button @click="handleInfo(scope.row)" type="info" size="small"></el-button>
            <el-button @click="handleDel(scope.row)" type="danger" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.page"
        :page-size="pagination.size"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total"
      >
      </el-pagination>
      <div style="margin-top: 20px">
        <el-button type="primary" size="small" @click="handleInstall()"></el-button>
        <el-button type="success" size="small" @click="handleTask()"></el-button>
        <el-button type="danger" size="small" disabled></el-button>
        <el-button type="danger" size="small" disabled></el-button>
        <el-button type="info" size="small" disabled></el-button>
      </div>
    </el-card>
    <h2>:</h2>
    <li></li>
    <li></li>
    <li>""</li>
    <li>""</li>
    <li>BootOSBootOS</li>
    <el-drawer
      :title="`: ${SysInfo.SN} `"
      :before-close="handleClose"
      :visible.sync="dialog"
      direction="rtl"
      size="70%"
      custom-class="demo-drawer"
      ref="drawer"
      :border="true"
    >
      <div class="demo-drawer__content">
        <el-descriptions class="margin-top" title="" :column="4" border size="small">
          <!-- <el-descriptions-item label="ID">100</el-descriptions-item> -->
          <el-descriptions-item label="SN">
            <el-tag size="medium"
              ><font size="6">{{ SysInfo.SN }}</font></el-tag
            >
          </el-descriptions-item>
          <el-descriptions-item label="">{{ SysInfo.Vender }}</el-descriptions-item>
          <el-descriptions-item label="">{{ SysInfo.Product }}</el-descriptions-item>
          <el-descriptions-item label="">{{ SysInfo.isVM ? '' : '' }}</el-descriptions-item>
          <el-descriptions-item label="CPU" :span="4">
            <el-table :data="SysInfo.CPU" border>
              <el-table-column prop="slot" label=""> </el-table-column>
              <el-table-column prop="model" label=""> </el-table-column>
              <el-table-column prop="core" label=""> </el-table-column>
            </el-table>
          </el-descriptions-item>
          <el-descriptions-item label="" :span="4">
            <el-table :data="SysInfo.MEM" border>
              <el-table-column prop="slot" label=""> </el-table-column>
              <el-table-column prop="size" label=""> </el-table-column>
              <el-table-column prop="description" label=""> </el-table-column>
            </el-table>
          </el-descriptions-item>
          <el-descriptions-item label="" :span="4">
            <el-table :data="SysInfo.ETH" border>
              <el-table-column prop="name" label=""> </el-table-column>
              <el-table-column prop="mac" label="MAC"> </el-table-column>
              <el-table-column prop="product" label=""> </el-table-column>
              <el-table-column prop="description" label=""> </el-table-column>
            </el-table>
          </el-descriptions-item>
          <el-descriptions-item label="" :span="4">
            <el-table :data="SysInfo.DISK" border>
              <el-table-column type="index" label="ID"></el-table-column>
              <el-table-column prop="product" label=""> </el-table-column>
              <el-table-column prop="type" label=""> </el-table-column>
              <el-table-column prop="size" label=""> </el-table-column>
            </el-table>
          </el-descriptions-item>
          <!-- <el-descriptions-item label=""> 03:00.0 Ethernet controller: VMware VMXNET3 Ethernet Controller (rev 01) </el-descriptions-item> -->
          <el-descriptions-item label="" :span="4">
            <el-table :data="SysInfo.RAID" border>
              <el-table-column type="index" label="ID"></el-table-column>
              <el-table-column prop="type" label=""> </el-table-column>
              <el-table-column prop="product" label=""> </el-table-column>
              <el-table-column prop="description" label=""> </el-table-column>
            </el-table>
          </el-descriptions-item>
          <el-descriptions-item label="IPMI" :span="4">
            <el-table :data="SysInfo.IPMI" border>
              <el-table-column prop="mac" label="MAC"> </el-table-column>
              <el-table-column prop="ipaddr" label="IP"> </el-table-column>
              <el-table-column prop="mask" label=""> </el-table-column>
              <el-table-column prop="gateway" label=""> </el-table-column> </el-table
          ></el-descriptions-item>
          <!-- <el-descriptions-item label="">
            {{ SysInfo.ctime | dateFmt }}
          </el-descriptions-item> -->
        </el-descriptions>
        <div class="demo-drawer__footer"></div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  created() {
    this.getHostList()
    this.cycleRunFunc(1, this.getHostList, 5000)
  },
  beforeDestroy() {
    if (this.intervalID) {
      clearInterval(this.intervalID)
    }
  },
  data() {
    return {
      hostList: [],
      multipleSelection: [],
      selectArrayMac: [],
      selectArray: [],
      intervalID: null,
      search: null,
      table: false,
      dialog: false,
      pagination: { page: 1, size: 20, total: 0 }, // 
      loading: false,
      formLabelWidth: '200px',
      timer: null,
      size: '',
      SysInfo: {}
    }
  },
  methods: {
    handleSizeChange(val) {
      console.log(` ${val}`)
    },
    handleCurrentChange(val) {
      console.log(` ${val}`)
      this.getHostList(val)
    },
    cycleRunFunc(status, func, time) {
      if (status) {
        // start == 1
        this.intervalID = setInterval(func, time)
      } else {
        // stop == 0
        clearInterval(this.intervalID)
        this.$message.success('')
      }
      // return setInterval(func, time)
    },
    async getHostList(page) {
      if (!page) {
        page = this.pagination.page
      }
      console.log('', page)
      const { data: response } = await this.$http.get(`install/discover/?page=${page}`)
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.hostList = response.results
      this.pagination = response.pagination
      console.log('')
      console.log('', this.$refs.multipleTable.selection)
      // 
      // 
      if (this.$refs.multipleTable.selection.length !== 0) {
        this.$refs.multipleTable.selection.forEach((row) => {
          console.log(JSON.stringify(this.hostList).indexOf(JSON.stringify(row))) // -1
          if (JSON.stringify(this.hostList).indexOf(JSON.stringify(row)) === -1) {
            this.$refs.multipleTable.selection.splice(this.$refs.multipleTable.selection.indexOf(row), 1)
          }
        })
      }
    },
    // handleSelectionChange(val) {
    //   console.log(this.$refs.multipleTable.selection)
    // },
    handleInstall() {
      console.log('', this.$refs.multipleTable.selection)
      if (this.$refs.multipleTable.selection.length !== 0) {
        this.$refs.multipleTable.selection.forEach(async (row) => {
          console.log('mac', row.mac)
          const { data: response } = await this.$http.post(`/install/discover/${row.mac}/convert/2/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success(`${row.mac}`)
          this.$refs.multipleTable.selection.shift() // 
          this.getHostList()
          console.log('')
        })
      } else {
        this.$message.error('')
      }
    },
    handleTask() {
      console.log('', this.$refs.multipleTable.selection)
      if (this.$refs.multipleTable.selection.length !== 0) {
        this.$refs.multipleTable.selection.forEach(async (row) => {
          console.log('mac', row.mac)
          const { data: response } = await this.$http.post(`/install/discover/${row.mac}/convert/1/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success(`${row.mac}`)
          this.$refs.multipleTable.selection.shift() // 
          this.getHostList()
          console.log('')
        })
      } else {
        this.$message.error('')
      }
    },
    handleEdit() {},
    handleDelete(row) {
      console.log(row.mac)
    },
    handleClose() {
      this.dialog = false
    },
    handleInfo(row) {
      this.SysInfo = row.sysinfo
      // this.SysInfo.ctime = row.date
      console.log(this.SysInfo)
      this.dialog = true
    },
    handleDel(row) {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          // center: true
        })
        .then(async () => {
          console.log(row.mac)
          const { data: response } = await this.$http.delete(`install/discover/${row.mac}/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.getHostList(this.pagination.page)
          this.$message.success('' + row.sn + ' ')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
    // async handleDel(row) {
    //   console.log(row.mac)
    //   this.$confirm(', ?', '', {
    //     confirmButtonText: '',
    //     cancelButtonText: '',
    //     type: 'warning'
    //   })
    //     .then(() => {
    //       this.$message({
    //         type: 'success',
    //         message: '!'
    //       })
    //     })
    //     .catch(() => {
    //       this.$message({
    //         type: 'info',
    //         message: ''
    //       })
    //     })
    // }
  }
}
</script>

<style lang="less" scoped>
