//   background: #4bd107;
// }

// .my-content {
//   background: #fde2fc;
// }

/deep/ .el-descriptions__body .el-descriptions__table {
  border-collapse: inherit;
  width: 98%;
  // table-layout: fixed;
}

/deep/ .el-drawer__body {
  margin-left: 2%;
}
</style>