<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <!-- <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item> -->
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder="" v-model="search"> <el-button slot="append" icon="el-icon-search"></el-button> </el-input>
        </el-col>
        <el-col :span="2"><el-button type="success" @click="getHostList()"></el-button></el-col>
      </el-row>
      <el-table ref="multipleTable" row-key="mac" :data="hostList" border style="width: 100%" @select="handleSelectionChange">
        <el-table-column type="selection" :reserve-selection="true" width="45"> </el-table-column>
        <el-table-column type="index" label="ID"></el-table-column>
        <el-table-column prop="sn" label="SN" width="180"> </el-table-column>
        <el-table-column prop="vender" label=""> </el-table-column>
        <el-table-column prop="product" label=""> </el-table-column>
        <el-table-column prop="os" label=""> </el-table-column>
        <el-table-column prop="config" label=""> </el-table-column>
        <el-table-column prop="clientip" label="Client IP"> </el-table-column>
        <el-table-column prop="ipmi" label="IPMI IP"> </el-table-column>
        <!-- <el-table-column prop="join_date" label="" width="300px"> </el-table-column> -->
        <el-table-column prop="join_date" label="" width="180">
          <template #default="{ row }">
            <span> {{ row.join_date | dateFmt }}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="200px">
          <template slot-scope="scope">
            <el-button @click="handleEdit(scope.row)" type="info" size="small"></el-button>
            <el-button @click="handleDel(scope.row)" type="danger" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.page"
        :page-size="pagination.size"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total"
      >
      </el-pagination>
      <div style="margin-top: 20px">
        <el-button type="primary" size="small" @click="handleInstall()"></el-button>
        <el-button type="success" size="small" @click="handleTask()"></el-button>
        <el-button type="danger" size="small" disabled></el-button>
        <!-- <el-button type="info" size="small"></el-button> -->
      </div>
      <h2></h2>
      <h2>""BootOS</h2>
    </el-card>
    <!--  @ -->
    <el-dialog title="" :visible.sync="editDialogFormVisible" @close="resetForm('editHost')">
      <el-form :model="editHostForm" :rules="editHostRules" ref="editHost" label-width="100px">
        <el-form-item label="">
          {{ editHostForm.sn }}
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="editHostForm.config" placeholder="" @change="handleConfigChange" style="width: 50%">
            <el-option
              v-for="item in configList"
              :key="item.id"
              :label="item.name"
              :value="{ name: item.name, path: item.path, image: item.image }"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="danger" @click="editDialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="editHost"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getHostList()
    this.getConfigList()
  },
  data() {
    return {
      hostList: [],
      multipleSelection: [],
      selectArray: [],
      search: null,
      pagination: { page: 1, size: 20, total: 0 }, // 
      editDialogFormVisible: false,
      editHostForm: {
        sn: '',
        mac: '',
        config: ''
      },
      editHostRules: {},
      configList: []
    }
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    handleSizeChange(val) {
      console.log(` ${val}`)
    },
    handleCurrentChange(val) {
      console.log(` ${val}`)
      this.getHostList(val)
    },
    async getHostList(page) {
      if (!page) {
        page = this.pagination.page
      }
      const { data: response } = await this.$http.get(`install/iprelist/?page=${page}`)
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.hostList = response.results
      this.pagination = response.pagination
      console.log('======')
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
      console.log('', this.multipleSelection)
    },
    handleInstall() {
      this.selectArray = []
      this.multipleSelection.forEach((row) => {
        this.selectArray.push(row.mac)
      })
      console.log('', this.selectArray)
      this.selectArray.forEach(async (mac) => {
        console.log('mac', mac)
        const { data: response } = await this.$http.post(`/install/iprelist/${mac}/install/`)
        if (response.code) {
          return this.$message.error(response.message)
        }
        this.$message.success(`${mac}`)
        this.getHostList()
      })
      this.selectArray = []
    },
    handleEdit(row) {
      console.log(row)
      this.editHostForm = row
      this.editDialogFormVisible = true
    },
    async getConfigList() {
      const { data: response } = await this.$http.get('temp/config/')
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.configList = response.results
      console.log(this.configList)
    },
    handleConfigChange() {},
    editHost() {
      console.log(this.editHostForm)
      const name = 'editHost'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const { data: response } = await this.$http.patch(`install/iprelist/${this.editHostForm.mac}/`, this.editHostForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.editDialogFormVisible = false
          this.getHostList(this.pagination.page)
          this.$message.success('')
        } else {
          this.$message.error('')
        }
      })
    },
    handleDelete() {},
    handlePxeboot() {},
    handleDel(row) {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          // center: true
        })
        .then(async () => {
          console.log(row.mac)
          const { data: response } = await this.$http.delete(`install/iprelist/${row.mac}/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.getHostList(this.pagination.page)
          this.$message.success('' + row.sn + ' ')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    handleTask() {
      console.log('', this.$refs.multipleTable.selection)
      if (this.$refs.multipleTable.selection.length !== 0) {
        this.$refs.multipleTable.selection.forEach(async (row) => {
          console.log('mac', row.mac)
          const { data: response } = await this.$http.post(`/install/iprelist/${row.mac}/convert/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success(`${row.mac}`)
          this.$refs.multipleTable.selection.shift() // 
          this.getHostList()
          console.log('')
        })
      } else {
        this.$message.error('')
      }
    }
  }
}
</script>

<style lang="less">
.my-label {
  background: #4bd107;
}

.my-content {
  background: #fde2fc;
}
</style>