<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder=""> <el-button slot="append" icon="el-icon-search"></el-button> </el-input>
        </el-col>
        <el-col :span="12"><el-button type="primary" @click="addConfigDialogVisible = true"></el-button></el-col>
      </el-row>
      <el-table :data="configList" border style="width: 100%">
        <el-table-column type="index" label="" width="50px"></el-table-column>
        <el-table-column prop="name" label=""> </el-table-column>
        <el-table-column prop="image" label=""> </el-table-column>
        <el-table-column prop="path" label=""> </el-table-column>

        <el-table-column fixed="right" label="" width="300px">
          <!-- <template> -->
          <template #default="{ row }">
            <el-button type="info" size="small" @click="viewHandler(row)"></el-button>
            <el-button type="primary" size="small" @click="editHandler(row)"></el-button>
            <el-button type="danger" size="small" @click="handleDel(row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.page"
        :page-size="pagination.size"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addConfigDialogVisible" @close="resetForm('addConfig')" :before-close="closeHandler" width="70%">
      <el-form ref="addConfig" :model="addConfigForm" :rules="addConfigRules" label-width="100px" border>
        <el-form-item label="" prop="name">
          <el-input v-model="addConfigForm.name" style="width: 50%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="image">
          <el-select v-model="addConfigForm.image" placeholder="" style="width: 50%">
            <el-option v-for="item in imageList" :key="item.id" :label="item.name" :value="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="KS" prop="config">
          <el-input type="textarea" v-model="addConfigForm.config" :rows="20"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-button type="primary" @click="generateURL(addConfigForm)"> ()</el-button>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addConfigDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addConfigHandler"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="viewConfigDialogVisible" @close="resetForm('viewConfig')" width="70%">
      <el-form ref="viewConfig" :model="viewConfigForm" label-width="100px">
        <el-form-item label="">
          <el-input v-model="viewConfigForm.name" style="width: 50%" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="viewConfigForm.image" style="width: 50%" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="viewConfigForm.path" style="width: 50%" disabled></el-input>
        </el-form-item>
        <el-form-item label="KS">
          <el-input type="textarea" :disabled="true" v-model="viewConfigForm.config" :rows="25"> </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="viewConfigDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editConfigDialogVisible" @close="resetForm('editConfig')" :before-close="closeHandler" width="70%">
      <el-form ref="editConfig" :model="editConfigForm" :rules="editConfigRules" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input v-model="editConfigForm.name" style="width: 50%" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="image">
          <el-input v-model="editConfigForm.image" style="width: 50%" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editConfigForm.path" style="width: 50%" disabled></el-input>
        </el-form-item>
        <el-form-item label="KS" prop="config">
          <el-input type="textarea" v-model="editConfigForm.config" :rows="20"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-button type="primary" @click="generateURL(editConfigForm)"> ()</el-button>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editConfigDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editConfigHandler()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getConfigList()
    this.getImageList()
  },
  data() {
    return {
      // 
      imageList: [],
      configList: [],
      // config
      addConfigDialogVisible: false,
      addConfigForm: {
        name: '',
        image: '',
        config: '',
        path: '',
        save_path: ''
      },
      addConfigRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 16, message: ' 4  16 ', trigger: 'blur' }
        ],
        image: [{ required: true, message: '', trigger: 'change' }],
        config: [{ required: true, message: '', trigger: 'blur' }]
      },
      // URL
      checkTag: 0,
      // config
      viewConfigDialogVisible: false,
      viewConfigForm: {
        name: '',
        image: '',
        config: '',
        path: ''
      },
      // config
      editConfigDialogVisible: false,
      editConfigForm: {
        image: '',
        config: ''
      },
      editConfigRules: {
        config: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      pagination: { page: 1, size: 20, total: 0 } // 
    }
  },
  methods: {
    // 
    async getImageList() {
      const { data: response } = await this.$http.get('temp/image/')
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.imageList = response.results
      console.log(this.imageList)
    },
    // 
    handleSizeChange(val) {
      console.log(` ${val}`)
    },
    handleCurrentChange(val) {
      console.log(` ${val}`)
      this.getConfigList(val)
    },
    // config
    async getConfigList(page) {
      if (!page) {
        page = this.pagination.page
      }
      const { data: response } = await this.$http.get(`temp/config/?page=${page}`)
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.configList = response.results
      this.pagination = response.pagination
      console.log(this.configList)
    },
    // config
    addConfigHandler() {
      if (!this.checkTag) {
        return this.$message.error('ks')
      }
      const name = 'addConfig'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          console.log(valid, '++++')
          console.log(this.addConfigForm)
          const { data: response } = await this.$http.post('temp/config/', this.addConfigForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success('')
          this.addConfigDialogVisible = false
          this.checkTag = 0
          this.getConfigList()
        } else {
          this.$message.error('')
        }
      })
    },
    handleImageChange() {
      console.log('123')
    },
    // ksurlURL
    async generateURL(Form) {
      console.log(Form)
      const { data: response } = await this.$http.post('temp/config/generate/', Form)
      if (response.code) {
        return this.$message.error(response.message)
      }
      // 1
      this.checkTag = 1
      this.$message.success('URL')
      console.log(Form)
      console.log(response)
      Form.config = response
      console.log(Form)
    },
    // form
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    // config
    viewHandler(row) {
      this.viewConfigForm = row
      this.viewConfigDialogVisible = true
    },
    // config
    editHandler(row) {
      this.editConfigForm = row
      console.log(this.editConfigForm)
      this.editConfigDialogVisible = true
    },
    //
    editConfigHandler() {
      if (!this.checkTag) {
        return this.$message.error('ks')
      }
      console.log(this.editConfigForm)
      const name = 'editConfig'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          console.log(valid, '++++')
          console.log(this.editConfigForm)
          const { data: response } = await this.$http.patch(`temp/config/${this.editConfigForm.id}/`, this.editConfigForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success('')
          this.editConfigDialogVisible = false
          this.checkTag = 0
          this.getConfigList()
        } else {
          this.$message.error('')
        }
      })
    },
    // 
    closeHandler() {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          // center: true
        })
        .then(() => {
          this.addConfigDialogVisible = false
          this.editConfigDialogVisible = false
          this.$message.success('')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // config
    handleDel(id) {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(async () => {
          const { data: response } = await this.$http.delete(`temp/config/${id}/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.getConfigList()
          this.$message.success('')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
.el-dialog .el-form .el-form.item .el-textarea__inner {
  min-height: 500px;
}
// 
/deep/ .el-textarea__inner {
  background-color: #303133;
  border-color: #e4e7ed;
  color: #f4f4f5;
  // cursor: not-allowed;
}
// 
/deep/ .el-textarea.is-disabled .el-textarea__inner {
  background-color: #303133;
  border-color: #e4e7ed;
  color: #f4f4f5;
  cursor: not-allowed;
}
/deep/ .el-input.is-disabled .el-input__inner {
  background-color: #e4e7ed;
  border-color: #e4e7ed;
  color: #303133;
  cursor: not-allowed;
}
</style>