<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder="" v-model="search"> <el-button slot="append" icon="el-icon-search"></el-button> </el-input>
        </el-col>
        <el-col :span="2"><el-button type="success" @click="getHostList()"></el-button></el-col>
      </el-row>
      <el-table ref="multipleTable" :data="hostList" border row-key="mac" style="width: 100%" @select="handleSelectionChange">
        <el-table-column type="selection" :reserve-selection="true" width="45"> </el-table-column>
        <el-table-column type="index" label="ID"></el-table-column>
        <el-table-column prop="sn" label="SN" width="180"> </el-table-column>
        <el-table-column prop="vender" label=""> </el-table-column>
        <el-table-column prop="product" label=""> </el-table-column>
        <el-table-column prop="clientip" label="Client IP"> </el-table-column>
        <el-table-column prop="ipmi" label="IPMI IP"> </el-table-column>
        <el-table-column prop="join_date" label="" width="180">
          <template #default="{ row }">
            <span> {{ row.join_date | dateFmt }}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="280px">
          <template #default="{ row }">
            <el-button @click="handleCommand(row)" type="primary" size="small"></el-button>
            <el-button @click="handlePlaybook(row)" type="success" size="small"></el-button>
            <el-button @click="handleDel(row)" type="danger" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.page"
        :page-size="pagination.size"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total"
      >
      </el-pagination>
      <div style="margin-top: 20px">
        <!-- <el-button type="primary" size="small" @click="handleInstall()"></el-button> -->
        <el-button type="success" size="small" @click="handleInstall()"></el-button>
        <el-button type="danger" size="small" disabled></el-button>
        <!-- <el-button type="info" size="small"></el-button> -->
      </div>
      <div class="info">
        <h2>BootOS</h2>
        <h2> ansible playbook</h2>
        <h2></h2>
        <h2></h2>
        <h2></h2>
      </div>
    </el-card>
    <!--  @ -->
    <el-dialog title="" :visible.sync="commandDialogFormVisible" :before-close="closeHandler" @close="resetForm('command')">
      <el-form :model="commandForm" :rules="commandRules" ref="command" label-width="100px">
        <el-form-item label="">
          {{ commandForm.sn }}
        </el-form-item>
        <el-form-item label="" prop="model">
          <el-input v-model="commandForm.model" placeholder="" style="width: 50%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="args">
          <el-input v-model="commandForm.args" placeholder="" style="width: 50%"></el-input>
          <div style="margin-top: 20px">
            <el-button type="danger" @click="clear()"></el-button>
            <el-button type="primary" @click="submitCommand()">{{ loadingtext }}</el-button>
          </div>
        </el-form-item>
      </el-form>
      <!-- <div class="result">
        <el-input type="textarea" v-model="commandResult" placeholder="" :rows="10"></el-input>
      </div> -->
      <div class="console">
        <div class="output" id="output" v-for="(output, index) in outputList" :key="index">
          <p v-html="output"></p>
          <!-- {{ output }} -->
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="danger" @click="commandDialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
    <!--  @ -->
    <el-dialog title="" :visible.sync="templateDialogVisible" @close="resetForm('template')" :before-close="closeHandler" width="30%">
      <el-form ref="template" :model="templateForm" :rules="templateRules" label-width="100px" border>
        <el-form-item label="">
          {{ templateForm.sn }}
        </el-form-item>
        <el-form-item label="" prop="tempid">
          <el-select v-model="templateForm.tempid" placeholder="" style="width: 50%">
            <el-option v-for="item in templateList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="templateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitPlaybook()"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import AnsiUp from 'ansi_up'
import { Loading } from 'element-ui'

export default {
  created() {
    this.getHostList()
    this.getTemplateList()
  },
  data() {
    return {
      loadingbutton: false,
      loadingtext: '',
      hostList: [],
      multipleSelection: [],
      selectArray: [],
      search: null,
      pagination: { page: 1, size: 20, total: 0 }, // 
      outputList: [],
      commandDialogFormVisible: false,
      commandForm: {
        sn: '',
        mac: '',
        model: '',
        args: ''
      },
      commandRules: {
        model: [{ required: true, message: '', trigger: 'blur' }]
        // args: [{ required: true, message: '', trigger: 'blur' }]
      },
      templateList: [],
      templateForm: {
        sn: '',
        mac: '',
        tempid: ''
      },
      templateDialogVisible: false,
      templateRules: {
        tempid: [{ required: true, message: '', trigger: 'change' }]
        // args: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    async getTemplateList() {
      const { data: response } = await this.$http.get('task/template/')
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.templateList = response.results
      console.log(this.templateList)
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.multipleSelection = []
      this.outputList = []
    },
    handleSizeChange(val) {
      console.log(` ${val}`)
    },
    handleCurrentChange(val) {
      console.log(` ${val}`)
      this.getHostList(val)
    },
    async getHostList(page) {
      if (!page) {
        page = this.pagination.page
      }
      const { data: response } = await this.$http.get(`task/hostlist/?page=${page}`)
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.hostList = response.results
      this.pagination = response.pagination
      console.log('======')
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
      console.log('', this.multipleSelection)
    },
    // handleInstall() {
    //   this.selectArray = []
    //   this.multipleSelection.forEach((row) => {
    //     this.selectArray.push(row.mac)
    //   })
    //   console.log('', this.selectArray)
    //   this.selectArray.forEach(async (mac) => {
    //     console.log('mac', mac)
    //     const { data: response } = await this.$http.post(`/install/iprelist/${mac}/install/`)
    //     if (response.code) {
    //       return this.$message.error(response.message)
    //     }
    //     this.$message.success(`${mac}`)
    //     this.getHostList()
    //   })
    //   this.selectArray = []
    // },
    handleCommand(row) {
      this.commandForm.sn = row.sn
      this.commandForm.mac = row.mac
      this.commandDialogFormVisible = true
    },
    submitCommand() {
      // this.loadingbutton = true
      this.loadingbutton = '...'
      console.log(this.commandForm)
      const name = 'command'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const loadingInstance = Loading.service({
            lock: true,
            text: '...',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.8)',
            target: document.querySelector('.el-dialog__body')
          })
          const { data: response } = await this.$http.post(`task/hostlist/${this.commandForm.mac}/command/`, this.commandForm)
          if (response.code) {
            this.$nextTick(() => {
              //  Loading 
              loadingInstance.close()
            })
            return this.$message.error(response.message)
          }
          // this.editDialogFormVisible = false
          // this.getHostList(this.pagination.page)
          this.$message.success('')
          const ansiUp = new AnsiUp()
          console.log(ansiUp)
          const outputs = response
          console.log(outputs)
          const html = ansiUp.ansi_to_html(outputs)
          console.log(html)
          this.outputList.push(html)
          this.$nextTick(() => {
            //  Loading 
            loadingInstance.close()
          })
          this.loadingbutton = false
          this.loadingtext = ''
        } else {
          this.fullscreenLoading = false
          this.$message.error('')
        }
      })
    },
    closeHandler() {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          // center: true
        })
        .then(() => {
          this.commandDialogFormVisible = false
          // this.editConfigDialogVisible = false
          this.$message.success('')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    clear() {
      this.outputList = []
    },
    handleEdit(row) {
      console.log(row)
      this.editHostForm = row
      this.editDialogFormVisible = true
    },
    async getConfigList() {
      const { data: response } = await this.$http.get('temp/config/')
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.configList = response.results
      console.log(this.configList)
    },
    handlePlaybook(row) {
      this.templateForm.sn = row.sn
      this.templateForm.mac = row.mac
      this.templateDialogVisible = true
    },
    submitPlaybook() {
      console.log(this.templateForm)
      const name = 'template'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const { data: response } = await this.$http.post(`task/hostlist/${this.templateForm.mac}/playbook/`, this.templateForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.templateDialogVisible = false
          this.getHostList(this.pagination.page)
          this.$message.success(response)
        } else {
          this.$message.error('')
        }
      })
    },
    editHost() {
      console.log(this.editHostForm)
      const name = 'editHost'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const { data: response } = await this.$http.patch(`install/iprelist/${this.editHostForm.mac}/`, this.editHostForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.editDialogFormVisible = false
          this.getHostList(this.pagination.page)
          this.$message.success('')
        } else {
          this.$message.error('')
        }
      })
    },
    handleDelete() {},
    handlePxeboot() {},
    handleDel(row) {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          // center: true
        })
        .then(async () => {
          console.log(row.mac)
          const { data: response } = await this.$http.delete(`task/hostlist/${row.mac}/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.getHostList(this.pagination.page)
          this.$message.success('' + row.sn + ' ')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    handleInstall() {
      console.log('', this.$refs.multipleTable.selection)
      if (this.$refs.multipleTable.selection.length !== 0) {
        this.$refs.multipleTable.selection.forEach(async (row) => {
          console.log('mac', row.mac)
          const { data: response } = await this.$http.post(`/task/hostlist/${row.mac}/convert/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success(`${row.mac}`)
          this.$refs.multipleTable.selection.shift() // 
          this.getHostList()
          console.log('')
        })
      } else {
        this.$message.error('')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.my-label {
  background: #4bd107;
}

.my-content {
  background: #fde2fc;
}
.console {
  width: 100%;
  height: 300px;
  background-color: #000;
  font-size: 15px;
  padding: 5px;
  color: #fff;
  white-space: pre-line;
}
</style>