<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder=""> <el-button slot="append" icon="el-icon-search"></el-button> </el-input>
        </el-col>
        <el-col :span="12"><el-button type="primary" @click="addImagedialogVisible = true"></el-button></el-col>
      </el-row>
      <el-table :data="imageList" border style="width: 100%">
        <el-table-column type="index" label="" width="50px"></el-table-column>
        <el-table-column prop="name" label=""> </el-table-column>
        <el-table-column prop="type" label=""> </el-table-column>
        <el-table-column prop="version" label=""> </el-table-column>
        <!-- <el-table-column prop="path" label="" width="600px"> </el-table-column> -->
        <el-table-column prop="path" label="" width="600px"> </el-table-column>
        <el-table-column fixed="right" label="" width="200px">
          <template #default="{ row }">
            <el-button @click="openNewPage(row.path)" type="primary" size="small"> </el-button>
            <el-button @click="handleDel(row.id)" type="danger" size="small"> </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.page"
        :page-size="pagination.size"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" width="35%" :visible.sync="addImagedialogVisible" @close="resetForm('addImages')" :before-close="closeHandler">
      <el-form ref="addImages" :model="addImageForm" :rules="addImageRules" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input v-model="addImageForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="addImageForm.type" placeholder="">
            <el-option label="CentOS" value="CentOS"></el-option>
            <!-- <el-option label="Ubuntu" value="Ubuntu" disabled></el-option>
            <el-option label="RHEL" value="RHEL"></el-option>
            <el-option label="Windows" value="Windows"></el-option> -->
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="version">
          <el-input v-model="addImageForm.version" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="path">
          <el-input v-model="addImageForm.path" placeholder=""> </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-button type="primary" @click="extractImage(addImageForm)"></el-button>
        </el-form-item>
        <!-- <el-upload
            class="upload-demo"
            ref="upload"
            :action="$http.defaults.baseURL + 'temp/upload/'"
            :data="this.addImageForm"
            :headers="getHeaders()"
            :limit="1"
            :on-remove="handleRemove"
            :on-success="handleSuccess"
            :file-list="fileList"
            :auto-upload="false"
          >
            <el-button slot="trigger" size="small" type="primary"></el-button>
            <el-button style="margin-left: 10px" size="small" type="success" @click="submitUpload"></el-button>
            <div slot="tip" class="el-upload__tip">ISO</div>
          </el-upload> -->
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeHandler()"> </el-button>
        <el-button type="primary" @click="handleAddImage"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Loading } from 'element-ui'
export default {
  created() {
    this.getImageList()
  },
  data() {
    return {
      imageList: [],
      addImagedialogVisible: false,
      // prop
      addImageForm: {
        name: '',
        type: '',
        version: '',
        path: '',
        save_path: ''
      },
      addImageRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 16, message: ' 4  16 ', trigger: 'blur' }
        ],
        type: [{ required: true, message: '', trigger: 'change' }],
        version: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 16, message: ' 4  16 ', trigger: 'blur' }
        ],
        path: [{ required: true, message: '', trigger: 'blur' }]
      },
      input3: '',
      fileList: [],
      imageTag: 1,
      pagination: { page: 1, size: 20, total: 0 } // 
    }
  },
  methods: {
    openNewPage(url) {
      window.open(url)
    },
    async getImageList(page) {
      if (!page) {
        page = this.pagination.page
      }
      console.log('', page)
      const { data: response } = await this.$http.get(`temp/image/?page=${page}`)
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.imageList = response.results
      this.pagination = response.pagination
      console.log('======')
    },
    handleSuccess(response, file, fileList) {
      this.fileList = [response]
      this.addImageForm.path = this.fileList[0].url
      this.addImageForm.save_path = this.fileList[0].save_path
    },
    handleRemove(file, fileList) {
      console.log(fileList, file)
      this.fileList = []
    },
    getHeaders() {
      return { Authorization: `Bearer ${window.localStorage.getItem('token')}` }
    },
    submitUpload() {
      console.log(this.$refs.upload.handleSuccess)
      this.$refs.upload.handleSuccess = this.handleSuccess
      console.log(this.$refs.upload.handleSuccess)
      this.$refs.upload.submit()
    },
    extractImage(form) {
      console.log(form)
      const name = 'addImages'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const loadingInstance = Loading.service({
            lock: true,
            text: '...',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.8)',
            target: document.querySelector('.el-dialog__body')
          })
          const { data: response } = await this.$http.post('temp/extract/', this.addImageForm)
          if (response.code) {
            this.$nextTick(() => {
              //  Loading 
              loadingInstance.close()
            })
            return this.$message.error(response.message)
          }
          this.addImageForm.path = response.url
          this.addImageForm.save_path = response.save_path
          this.imageTag = 0
          this.$nextTick(() => {
            //  Loading 
            loadingInstance.close()
          })
          this.$message.success('')
        } else {
          this.$message.error('')
        }
      })
    },
    handleAddImage() {
      const name = 'addImages'
      this.$refs[name].validate(async (valid) => {
        if (this.imageTag) {
          return this.$message.error('')
        }
        if (valid) {
          console.log(valid, '+++++')
          console.log(this.addImageForm)
          const { data: response } = await this.$http.post('temp/image/', this.addImageForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success('')
          this.addImagedialogVisible = false
          this.resetForm('addImages')
          this.getImageList()
        } else {
          this.$message.error('')
        }
      })
    },
    handleSizeChange(val) {
      console.log(` ${val}`)
    },
    handleCurrentChange(val) {
      console.log(` ${val}`)
      this.getImageList(val)
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.fileList = []
      this.imageTag = 1
    },
    closeHandler() {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          // center: true
        })
        .then(() => {
          this.addImagedialogVisible = false
          this.resetForm('addImages')
          this.$message.success('')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    handleDel(id) {
      this.$msgbox
        .confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(async () => {
          const { data: response } = await this.$http.delete(`temp/image/${id}/`)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.getImageList(this.pagination.page)
          this.$message.success('')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
    // async handleDel(row) {
    //   console.log(row.mac)
    //   this.$confirm(', ?', '', {
    //     confirmButtonText: '',
    //     cancelButtonText: '',
    //     type: 'warning'
    //   })
    //     .then(() => {
    //       this.$message({
    //         type: 'success',
    //         message: '!'
    //       })
    //     })
    //     .catch(() => {
    //       this.$message({
    //         type: 'info',
    //         message: ''
    //       })
    //     })
    // }
  }
}
</script>

<style lang="less" scoped>
.el-dialog .el-form-item .el-select {
  width: 80%;
}
.el-dialog .el-form-item .el-input {
  width: 80%;
}
</style>