<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <!-- <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item> -->
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder=""> <el-button slot="append" icon="el-icon-search"></el-button> </el-input>
        </el-col>
        <el-col :span="12"><el-button type="primary" @click="addHwdialogVisible = true"></el-button></el-col>
      </el-row>
      <el-table :data="hwList" border style="width: 100%">
        <el-table-column prop="name" label=""> </el-table-column>
        <el-table-column prop="vendor" label=""> </el-table-column>
        <el-table-column prop="model" label=""> </el-table-column>
        <el-table-column fixed="right" label="" width="200px">
          <template>
            <el-button @click="addHwdialogVisible = true" type="info" size="small"></el-button>
            <el-button type="danger" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.page"
        :page-size="pagination.size"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addHwdialogVisible" @close="resetForm('addHw')">
      <el-form ref="addHw" :model="addHwForm" :rules="addUserRules" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input v-model="addHwForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="vendor">
          <el-input v-model="addHwForm.vendor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="model">
          <el-input v-model="addHwForm.model"></el-input>
        </el-form-item>
        <el-form-item label="RAID" prop="raid">
          <el-table :data="addHwForm.raid" border>
            <el-table-column prop="model" label=""> </el-table-column>
            <el-table-column prop="disks" label=""> </el-table-column>
            <el-table-column prop="info" label=""> </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item label="IPMI" prop="ipmi">
          <el-table :data="addHwForm.ipmi" border>
            <el-table-column prop="ipaddr" label=""> </el-table-column>
            <el-table-column prop="username" label=""> </el-table-column>
            <el-table-column prop="password" label=""> </el-table-column>
          </el-table>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addHwdialogVisible = false"> </el-button>
        <el-button type="primary"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getUserList()
  },
  data() {
    return {
      addHwdialogVisible: false,
      // prop
      addHwForm: {
        name: '',
        vendor: '',
        model: '',
        raid: [
          {
            model: 'RAID_1',
            disks: '0 1',
            info: ''
          },
          {
            model: 'RAID_5',
            disks: '2 3 4 , 5',
            info: '5'
          }
        ],
        ipmi: [
          {
            ipaddr: 'static',
            username: 'root',
            password: 'calvin'
          }
        ]
      },
      input3: '',
      hwList: [
        {
          name: 'Dell-R740-Hadoop',
          vendor: 'Dell',
          model: 'R740'
        },
        {
          name: 'Huawei-RH2288HV3-Hadoop',
          vendor: 'Huawei',
          model: 'RH2288H_V3'
        },
        {
          name: 'VM-Test',
          vendor: 'VM',
          model: ''
        }
      ],
      pagination: { page: 1, size: 20, total: 0 } // 
    }
  },
  methods: {
    async getUserList(page = 1) {
      if (!page) {
        page = 1
      }
      const { data: response } = await this.$http.get(`users/?page=${page}`)
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.userList = response.results
      this.pagination = response.pagination
    },
    handleSizeChange(val) {
      console.log(` ${val}`)
    },
    handleCurrentChange(val) {
      console.log(` ${val}`)
      this.getUserList(val)
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    addUser() {
      console.log(this.addImageForm)
      const name = 'addUser'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          console.log(valid, '++++')
          const { data: response } = await this.$http.post('users/', this.addImageForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success('')
          this.addHwdialogVisible = false
          this.resetForm(name)
          this.getUserList() // 
        } else {
          this.$message.error('')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
</style>