      <div class="user">
        <!--  -->
        <div class="user-avator">
          <img src="../assets/logo.png" />
        </div>
        <!--  -->
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link">{{ user.username }}<i class="el-icon-arrow-down el-icon--right"></i> </span>
          <el-dropdown-menu slot="dropdown">
            <!-- <el-dropdown-item command="chpwd"></el-dropdown-item> -->
            <el-dropdown-item divided command="logout"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!--  @ -->
        <el-dialog title="" :visible.sync="chpwdDialogFormVisible" @close="resetForm('chpwdUser')">
          <el-form :model="chpwdUserForm" :rules="chpwdUserRules" ref="chpwdUser" label-width="100px">
            <el-form-item label="">
              {{ user.username }}
            </el-form-item>
            <el-form-item label="" prop="oldPassword">
