            <el-form-item label="" prop="checkpass">
              <el-input v-model="chpwdUserForm.checkpass"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button type="danger" @click="chpwdDialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="chpwdUser(user.id)"> </el-button>
          </div>
        </el-dialog>
