              <i class="el-icon-s-home"></i>
              <span></span>
            </template>
            <el-menu-item index="/dashboard">Dashboard</el-menu-item>
            <el-menu-item index="/discovered"></el-menu-item>
          </el-submenu>

          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-monitor"></i>
              <span></span>
            </template>
            <el-menu-item index="/presettings" disabled></el-menu-item>
            <el-menu-item index="/prelist"></el-menu-item>
            <el-menu-item index="/installing"></el-menu-item>
            <el-menu-item index="/installed"></el-menu-item>
          </el-submenu>

          <el-submenu index="3">
            <template slot="title">
              <i class="el-icon-suitcase-1"></i>
              <span></span>
            </template>
            <el-menu-item index="/hostlist"></el-menu-item>
            <el-menu-item index="/tasktemps"></el-menu-item>
