<script>
export default {
  // 
  created() {
    // this.getMenuList()
    // this.getUserInfo()
    console.log(this.$route.path, '000')
  },
  // 
  data() {
    // 
    const validateNewPass = (rule, value, callback) => {
      if (value !== this.chpwdUserForm.newPassword) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    // 
    const validateOldpass = (rule, value, callback) => {
      if (value === this.chpwdUserForm.oldPassword) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      menulist: [],
      isCollapse: false,
      user: {},
      // 
      chpwdUserForm: {
        oldPassword: '',
        newPassword: '',
        checkpass: ''
      },
      chpwdDialogFormVisible: false,
      chpwdUserRules: {
        oldPassword: [{ required: true, message: '', trigger: 'blur' }],
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 16, message: ' 4  16', trigger: 'blur' },
          { validator: validateOldpass, trigger: 'blur' }
        ],
        checkpass: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateNewPass, trigger: 'blur' }
        ]
      } // 
    }
  },
  // 
  methods: {
    resetForm(formName) {
      // 
      this.$refs[formName].resetFields()
    },
    logout() {
      window.localStorage.removeItem('token')
      this.$message('')
      this.$router.push('/login')
    },
    // 
    async getMenuList() {
      const { data: response } = await this.$http.get('users/menulist/')
      //   console.log(response)
      //   console.log(response.code)
      // codereturn message
      if (response.code) {
        return this.$message.error(response.message)
      }
      // response.data => menulist
      this.menulist = response.data
      console.log(this.menulist)
    },
    // 
    caidan() {
      this.isCollapse = !this.isCollapse
    },
    // 
    handleCommand(command) {
      if (command === 'chpwd') {
        this.chpwdDialogFormVisible = true
      } else if (command === 'logout') {
        this.logout()
      }
    },
    // 
    chpwdUser(id) {
      const name = 'chpwdUser'
      this.$refs[name].validate(async (valid) => {
        if (valid) {
          const { data: response } = await this.$http.post(`users/mgr/${id}/setpwd/`, this.chpwdUserForm)
          if (response.code) {
            return this.$message.error(response.message)
          }
          this.$message.success('')
          this.chpwdDialogFormVisible = false
        }
      })
    },
    async getUserInfo() {
      const { data: response } = await this.$http.get('users/mgr/whoami/')
      if (response.code) {
        return this.$message.error(response.message)
      }
      this.user = response.user
    }
  }
}
</script>

